import React, { Component } from "react";
import _ from "lodash";
import moment from "moment";
import { connect } from "react-redux";
import ReactDatetime from "react-datetime";
import { toast } from "react-toastify";
import {
  Container,
  Button,
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
} from "reactstrap";
import ErrorSummary from "components/ErrorSummary";
import { FormInput } from "components/Form";
import { getDate, fixNumber } from "helpers/UtilsHelper";
import SelectProvider from "./SelectProvider";
import invoiceApi from "../../../../api/invoiceApi";

import "./CreateInvoice.scss";

let today = moment().startOf("date");

const minValueErrorMessage = "Amount must large than 0.";
const requiredMessage = "This Field is required.";

class CreateInvoice extends Component {
  constructor(props) {
    super(props);
    this.state = {
      provider: "",
      date: today,
      comment: "",
      amount: "",
      errors: {},
      formError: {},
      isFormReady: {},
    };
  }

  handleSelectedProvider = (e) => {
    delete this.state.errors["provider"];
    delete this.state.formError["provider"];
    this.setState({ provider: e.target.value });
  };

  handleSelectedDate = (name, value) => {
    this.setState({ [name]: value });
  };

  handleChange = (e) => {
    const { value, name } = e.target;
    const { formError } = this.state;
    delete this.state.errors[name];
    delete this.state.formError[name];
    if (name === "amount") {
      if (Number(value) <= 0) {
        this.setState({
          formError: { ...formError, amount: [minValueErrorMessage] },
        });
      }
    }
    this.setState({ [name]: value });
  };

  checkErrors = (data) => {
    const { errors, formError } = this.state;
    let newError = {};
    let providerError = {};
    for (const key in data) {
      if (data[key] === "") {
        newError[key] = [requiredMessage];
      }
      if (data["amount"] <= 0) {
        newError["amount"] = [minValueErrorMessage];
      }
      if (!data["provider"]) {
        providerError["provider"] = [requiredMessage];
      }
      this.setState({
        errors: { ...errors, ...providerError },
        formError: { ...formError, ...newError },
      });
    }
  };

  createInvoice = async () => {
    const { provider, date, comment, amount, errors, formError, isFormReady } =
      this.state;
    const data = {
      provider,
      date: date ? getDate(date._d) : "",
      comment: comment.trim(),
      amount,
    };
    await this.checkErrors(data);
    if (_.isEmpty(formError) && _.isEmpty(errors) && isFormReady.check) {
      invoiceApi
        .createManualInvoice(data)
        .then(() => {
          toast.success("Create Invoice success");
          this.props.history.push("/admin/billings/manual-invoice");
        })
        .catch((err) => {
          this.setState({ errors: err.response.data.errors });
          toast.error("Create Invoice fail");
        });
    }
  };

  handleError = (field, status) => {
    if (status) {
      let check = true;
      for (let key in this.state.isFormReady) {
        if (key !== field && key !== "check") {
          check = check && this.state.isFormReady[key];
        }
      }
      this.setState({
        isFormReady: { ...this.state.isFormReady, [field]: true, check: check },
      });
    } else {
      this.setState({
        isFormReady: {
          ...this.state.isFormReady,
          [field]: false,
          check: false,
        },
      });
    }
  };

  goBack = () => {
    this.props.history.goBack();
  };

  render() {
    const { token } = this.props;
    const { formError } = this.state;
    return (
      <Container className="create-invoice">
        <Card>
          <CardHeader>
            <Row className="d-flex">
              <Col
                md={6}
                xs={12}
                className="d-flex justify-content-center align-items-center"
              >
                <h2>
                  <b className="text-primary">EtheraPay</b>
                </h2>
              </Col>
              <Col md={6} xs={12} className="mt-3 mt-md-0">
                <Row className="d-flex align-items-center justify-content-center">
                  <b className="text-primary mr-3">Provider:</b>
                  <div className="width-200">
                    <SelectProvider
                      token={token}
                      handleSelectedProvider={this.handleSelectedProvider}
                      provider={this.state.provider}
                    />
                  </div>
                </Row>
              </Col>
            </Row>
          </CardHeader>
          <CardBody>
            <Row className="tb-custom">
              <Col md={4} lg={3}>
                <Row className="header">
                  <b>Date</b>
                </Row>
                <Row className="body">
                  <ReactDatetime
                    className="rdt"
                    id="date"
                    defaultValue={this.state.date}
                    inputProps={{
                      placeholder: "MM/DD/YYYY",
                    }}
                    closeOnSelect={true}
                    dateFormat={true}
                    timeFormat={false}
                    onChange={(date) => this.handleSelectedDate("date", date)}
                  />
                </Row>
              </Col>
              <Col md={6} lg={7}>
                <Row className="header">
                  <b>Comment</b>
                </Row>
                <Row className="body">
                  <FormInput
                    type="text"
                    name="comment"
                    handleError={this.handleError}
                    errors={formError["comment"]}
                    onChange={this.handleChange}
                    required={true}
                    defaultValue={this.state.comment || ""}
                    placeholder="Type comment..."
                  />
                </Row>
              </Col>
              <Col md={2} lg={2}>
                <Row className="header">
                  <b>Amount</b>
                </Row>
                <Row className="body">
                  <FormInput
                    type="number"
                    step="0.01"
                    name="amount"
                    handleError={this.handleError}
                    errors={formError["amount"]}
                    onChange={this.handleChange}
                    required={true}
                    defaultValue={this.state.amount || ""}
                    placeholder="Type amount..."
                  />
                </Row>
              </Col>
            </Row>
          </CardBody>
          <ErrorSummary error={this.state.errors} className="mt-3 ml-3" />
          <CardFooter>
            <Col sm={12} className="text-center">
              <span className="font-weight-bold">
                Total Amount Due: ${fixNumber(this.state.amount)}
              </span>
            </Col>
            <Row className="mt-3">
              <Col className="text-center">
                <Button color="secondary" onClick={this.goBack}>
                  Cancel
                </Button>
              </Col>
              <Col className="text-center">
                <Button color="info" onClick={this.createInvoice}>
                  Save
                </Button>
              </Col>
            </Row>
          </CardFooter>
        </Card>
      </Container>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    token: state.auth.token,
  };
};

export default connect(mapStateToProps, null)(CreateInvoice);
