import React from 'react';
import Select2 from 'react-select2-wrapper'
import { getUserFullName } from 'helpers/UtilsHelper';
import config from '../../../../config'

export default function SelectProvider(props) {
    const { token, handleSelectedProvider, provider, } = props
    return <Select2
        className="form-control"
        defaultValue={provider}
        onSelect={handleSelectedProvider}
        options={{
            placeholder: "Select provider",
            ajax: {
                url: `${config.API_END_POINT}/providers`,
                headers: {
                    "Authorization": "Bearer " + token,
                    "Content-Type": "application/json",
                },
                processResults: function (data) {
                    const listProviderData = data.result.map(item => {
                        return {
                            id: item.id,
                            text: getUserFullName(item)
                        }
                    })
                    return {
                        results: listProviderData
                    };
                },
                data: function (params) {
                    var query = {
                        search: params.term,
                    }
                    return query;
                }
            }
        }}
    />
}